:root {
  --width: 200px;
  --height: 100px;
  --heightHalf: 50px;
  --widthMinusHalfHeight: 150px;
  
}
 .container {
     position: relative;
	 width: var(--width);
	 height: var(--height);
	 perspective: 1000px;
}
 [dir] .container {
     margin-top: 200px !important;
}
 .button {
	 display: block;
	 width: inherit;
	 height: inherit;
	 transform-style: preserve-3d;
}
 .button:hover .button_side:after {
	 opacity: 1;
}
 .button_side {
	 position: absolute;
	 width: inherit;
     height: inherit;
     color: white;
     
	 line-height: var(--height);
	 font-size: 24px;
	 font-weight: bold;
	 display: block;
	 box-sizing: border-box;
	 transition: 0.3s ease;
	 opacity: 1;
}
 [dir] .button_side {
     border: 1px solid white;
	 text-align: center;
}
 .button_side:after {
	 width: 100%;
	 height: 100%;
	 content: '';
	 top: 0;
	 position: absolute;
	 z-index: -1;
	 opacity: 0;
	 transition: opacity 0.3s ease;
}
 [dir=ltr] .button_side:after {
  background: linear-gradient(to right, #24c6dc, #514a9d);
  left: 0;
  animation: 15s hue infinite;
}
 [dir=rtl] .button_side:after {
	 background: linear-gradient(to left, #24c6dc, #514a9d);
	 right: 0;
	 animation: 15s hue infinite;
}
 .button_front {
   /* transform: translateZ(var(--height)/2); */
}
 [dir] .button_front {
   transform: translateZ(var(--heightHalf));
}
 .button_back {
  /* transform: rotateY(-180deg) translateZ(var(--height)); */
}
 [dir] .button_back {
	 transform: rotateY(-180deg) translateZ(var(--heightHalf));
}
 .button_right {
   width: var(--height);
	 /* transform: rotateY(90deg) translateZ(var(--width) - var(--height)/2); */
}
 [dir] .button_right {
   transform: rotateY(90deg) translateZ(var(--widthMinusHalfHeight));
}
 .button_left {
   width: var(--height);
	 /* transform: rotateY(-90deg) translateZ(var(--height)/2); */
}
 [dir] .button_left {
   transform: rotateY(-90deg) translateZ(var(--heightHalf));
}
 .button_top {
	 /* transform: rotateX(90deg) translateZ(var(--height)/2); */
}
 [dir] .button_top {
  transform: rotateX(90deg) translateZ(var(--heightHalf));
}
 [dir] .button_bottom {
	 transform: rotateX(-90deg) translateZ(var(--heightHalf));
}
 .button_glow {
	 position: absolute;
	 content: '';
	 height: 100%;
	 width: 100%;
	 opacity: 1;
}
 [dir=ltr] .button_glow {
  background: linear-gradient(to right, #24c6dc, #514a9d);
  animation: 15s hue-blur infinite;
}
 [dir=rtl] .button_glow {
	 background: linear-gradient(to left, #24c6dc, #514a9d);
	 animation: 15s hue-blur infinite;
}
 @keyframes hue-blur {
	 0% {
		 filter: blur(30px) hue-rotate(0deg);
	}
	 50% {
		 filter: blur(80px) hue-rotate(360deg);
	}
	 100% {
		 filter: blur(30px) hue-rotate(0deg);
	}
}
 @keyframes hue {
	 0% {
		 filter: hue-rotate(0deg) blur(30px);
	}
	 50% {
		 filter: hue-rotate(360deg) blur(30px);
	}
	 100% {
		 filter: hue-rotate(0deg) blur(30px);
	}
}
 
